@charset "utf-8";

// ==================================
//
// ==================================

//  -----------------------
	$colorType01: #FFF;	//
	$colorType02: #f2efea;	// 
	$colorType03: #214b63;	// 
	$colorType04: #ae9958;	//2 
	$colorType05: #e28691;	// 
	$colorType06: #19ac8b;	//  
	$colorType07: #c6b689;	//   
	$colorType08: #f98384;	//  
	$colorType09: #f18f4d;	//  
	$colorType10: #995ad4;	//  
	$colorType11: #3966d3;	//  

	$color-link: #ae9958;	//
	$color-link-l: #dbd1b6;	//
	$color-link-d: #7a6116;	//

//  -----------------------
	$sansSerif: "","Meiryo"," ","Hiragino Kaku Gothic Pro"," Pro W3",sans-serif;
	$serif: "", YuMincho, " ProN W3", "Hiragino Mincho ProN", "HGE", " ", " ", serif;

//  -----------------------
	$spaceSize: 1.8rem;

//  -----------------------
	$breakpoints: (
		xs:"print, screen and (min-width: 321px)",
		s:"print, screen and (min-width: 576px)",
		m:"print, screen and (min-width: 768px)",
		l:"print, screen and (min-width: 992px)",
		xl:"print, screen and (min-width: 1200px)",
	);
	@mixin media($breakpoint){
		@media #{map-get($breakpoints,$breakpoint)}{
			@content;
		}
	}

// z-index  -----------------------
	$layer: (
		modal: 999,
		header: 20,
		tooltip: 10,
		default: 1
	);

// pxem  16-----------------------
	@function em($px, $base: 16) {
		$value: $px;
		$value: calc($px/$base) * 1em;
		@return $value;
	}

//  -----------------------
	$pageNameList: company, contact;